<template>
    <div class="sidebar">
        <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" unique-opened router>
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.index">
                        <template slot="title"><i :class="item.icon"></i>{{ item.title }}</template>
                        <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">{{ subItem.title }}
                        </el-menu-item>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index">
                        <i :class="item.icon"></i>{{ item.title }}
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                items: [
                    {
                        icon: 'el-icon-setting',
                        index: 'readme',
                        title: '自述'
                    },
                    
                    {

                        icon: 'el-icon-star-on',
                        index: '2',
                        title: '项目管理',
                        subs: [
                            {
                                index: 'adminLists',
                                title: '管理员列表'
                            },
                            {
                                index: 'User',
                                title: '用户列表'
                            },
                            /*{
                                index: 'Member',
                                title: '会员列表'
                            },*/
                            

                            
                            
                        ]



                    },
                    {
                        icon: 'el-icon-menu',
                        index: 'Menu',
                        title: '菜单管理'
                    },
                                        
                    
                    {
                        icon: 'el-icon-menu',
                        index: 'content',
                        title: '文章管理'
                    },

                    /*{
                        icon: 'el-icon-menu',
                        index: 'Category',
                        title: '商品类别管理'
                    },

                    {
                        icon: 'el-icon-menu',
                        index: 'Product',
                        title: '商品管理'
                    },

                    {
                        icon: 'el-icon-menu',
                        index: 'Order',
                        title: '订单管理'
                    },

                    {
                        icon: 'el-icon-menu',
                        index: 'Theme',
                        title: '主题管理'
                    },

                    {
                        icon: 'el-icon-menu',
                        index: 'ThemeContent',
                        title: '主题内容管理'
                    },

                    {
                        icon: 'el-icon-menu',
                        index: 'MemberCard',
                        title: '会员卡管理'
                    },
                    {
                        icon: 'el-icon-menu',
                        index: 'Coupon',
                        title: '优惠券管理'
                    },*/

                    {

                        icon: 'el-icon-star-on',
                        index: '3',
                        title: '预约管理',
                        subs: [
                            {
                                index: 'BookMenu',
                                title: '预约标签管理'
                            },
                            {
                                index: 'Book',
                                title: '预约内容管理'
                            },
                            {
                                index: 'BookOrder',
                                title: '预约订单管理'
                            },
                            {
                                index: 'BookRemark',
                                title: '预约评论管理'
                            },
                            

                            
                            
                        ]



                    },
                    /*{

                        icon: 'el-icon-star-on',
                        index: '4',
                        title: '帖子管理',
                        subs: [
                            {
                                index: 'InvitationMenu',
                                title: '帖子类型管理'
                            },
                            {
                                index: 'Invitation',
                                title: '帖子内容管理'
                            },
                            

                            
                            
                        ]



                    },*/
                    
                ]
            }
        },
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            }
        }
    }
</script>

<style scoped>
    .sidebar{
        display: block;
        position: absolute;
        width: 250px;
        left: 0;
        top: 70px;
        bottom:0;
        background: #2E363F;
    }
    .sidebar > ul {
        height:100%;
    }
</style>



