<template>
    <div class="header">
        <div class="logo">后台管理系统1.0</div>
        <div class="user-info">
            <el-dropdown trigger="click" @command="handleCommand">
                <span class="el-dropdown-link">
                    <img class="user-logo"  :src="headImgUrl">
                    {{username}}
                    
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="loginout">退出</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                defaultName: '数据有误',
                defaultHeadImgUrl: '../../../static/img/img.jpg',
            }
        },
        computed:{
            username(){
                let username = this.$store.getters.getUserInfo.username;
                return username ? username : this.defaultName;
            },
            headImgUrl(){
                let userInfo = this.$store.getters.getUserInfo.userInfo;
                if(JSON.parse(userInfo).img[0]){
                    console.log('executed');
                    let headImgUrl = JSON.parse(userInfo).img[0].url;
                    return headImgUrl;
                    
                }else{
                    
                    let headImgUrl = this.defaultHeadImgUrl;
                    return headImgUrl ;
                };
                
                
                
                

            }
        },
        methods:{
            handleCommand(command) {
                if(command == 'loginout'){
                    
                    this.$store.commit('logout');
                    this.$router.push('/login');
                }
            }
        }
    }
</script>
<style scoped>
    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        line-height: 70px;
        color: #fff;
    }
    .header .logo{
        float: left;
        width:250px;
        text-align: center;
    }
    .user-info {
        float: right;
        padding-right: 50px;
        font-size: 16px;
        color: #fff;
    }
    .user-info .el-dropdown-link{
        position: relative;
        display: inline-block;
        padding-left: 50px;
        color: #fff;
        cursor: pointer;
        vertical-align: middle;
    }
    .user-info .user-logo{
        position: absolute;
        left:0;
        top:15px;
        width:40px;
        height:40px;
        border-radius: 50%;
    }
    .el-dropdown-menu__item{
        text-align: center;
    }
</style>



